<script>
import api from '@/api'
import { getToken } from '@/api/auth'

export default ({
  inject: ['reload'],
  data () {
    return {
      ortelbox: [],
      columns: [
          {
            title: 'ID',
            key: 'id',
            width: 100, 
            align: 'center'
          },
          {
            title: '名称',
            key: 'game_name',
            width: '', 
            align: 'center'
          },
          {
            title: 'id',
            key: 'game_id',
            width: '', 
            align: 'center'
          },
          {
            title: '手机号码',
            key: 'phone',
            width: 200, 
            align: 'center'
          },
          {
            title: 'ip',
            key: 'ip',
            width: 100, 
            align: 'center'
          },
          {
            title: '预约系统类型',
            key: 'system',
            width: 100, 
            align: 'center'
          },
          {
            title: '预约时间',
            key: 'addtime',
            width: 200, 
            align: 'center'
          },
      ],
      data: [],
      formData: {
        classify: '1',
        seltype: 'game_name',
        selval: '',
        phone: '',
      },
      isLoading: true,
      page: 1,
      per_page: 10,
      totalPages: 0,
      params: {
      },
      warnModal: false,
      modal_loading: false,
      telSelval: '',
      idnameSelval: '',
      addtime: ''
    }
    },
    mounted () {
      this.getData();
    },
    methods: {
      getData() {
        const token = getToken(),
              classify = this.formData.classify || 1,
              seltype = this.formData.seltype || 0,
              selval = this.formData.selval || '',
              phone = this.formData.phone || '',
              page = this.page,
              per_page = this.per_page,
              params = {token, classify, seltype, selval, phone, page, per_page,}
              
        // console.log(params)
        api.reservationlist(params).then(res => {
            if(res.code === 0) {
              this.data = res.data.list
              this.totalPages = res.data.totlerows
              this.isLoading = false 
              // console.log(this.data)
            } else {
              this.$Notice.error({title: res.msg})
            }
          })
      },
      changePage: function(page) {
        this.page = page;
        this.getData();
      },
      changePageNum: function(num) {
        this.per_page = num;
        this.getData();
      },
      // txt文件导出
      onClickDownDaily() {
      const token = getToken(),
            classify = this.formData.classify || 1,
            params = {token, classify,}
            let yy = new Date().getFullYear();
            let mm = new Date().getMonth()+1;
            let dd = new Date().getDate();
            let hh = new Date().getHours();
            let mf = new Date().getMinutes()<10 ? '0'+new Date().getMinutes() : new Date().getMinutes();
            let ss = new Date().getSeconds()<10 ? '0'+new Date().getSeconds() : new Date().getSeconds();
            const gettime = yy+'-'+mm+'-'+dd+' '+hh+':'+mf+':'+ss;
          api.getReservationTelList(params).then(res => {
            //数据导出
            const blob = new Blob([res])
            const url = window.URL.createObjectURL(blob)
            const aLink = document.createElement('a')
            aLink.style.display = 'none'
            aLink.href = url
            aLink.setAttribute('download', this.$store.state.itemName+'-'+gettime+'-'+token+'.txt')
            document.body.appendChild(aLink)
            aLink.click()
            document.body.removeChild(aLink) // 下载完成移除元素
            window.URL.revokeObjectURL(url) // 释放掉blob对象
          })
    },
    }
})
</script>

<template>
    <div>
        <h3><Icon type="md-barcode" size=24 />预约列表</h3>
        
        <div class="formBox">
            <Select v-model="formData.classify" style="width:100px">
                <Option value="1">游戏</Option>
                <Option value="2">应用</Option>
            </Select>
            <span> : </span>
            <Select v-model="formData.seltype" style="width:100px">
                <Option value="game_name">名称</Option>
                <Option value="game_id">ID</Option>
            </Select>

            <div class="idbox">
              <span></span><Input  v-model="formData.selval" clearable style="width: 200px" />
            </div>

            <div class="idbox">
              <span>手机号码/QQ号 : </span><Input  v-model="formData.phone" clearable style="width: 200px" />
            </div>

            <Button type="primary" style="width:100px" @click="getData()">
                <Icon type="ios-search" size=16></Icon>
                搜索
            </Button>

            <Button icon="ios-download-outline" type="info" @click="onClickDownDaily">下载预约列表</Button>
        </div>

        <Table 
            border 
            ref="selection" 
            :columns="columns" 
            :data="data" 
            :loading=isLoading
            >
        </Table>

        <div style="margin: 10px;overflow: hidden">
            <div style="float: right;">
                <Page :total="totalPages*1" show-total show-elevator show-sizer @on-page-size-change="changePageNum" @on-change="changePage" />
            </div>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .formBox {
        height: 50px;
        margin-bottom: 20px;
        .searchBar{
            display: contents;
            padding-right: 20px;
        }
        .idbox {
            display:contents;
            span{
                margin-left:20px; 
            }
        }
        Button{
            margin-left: 20px;
            margin-right: 20px;
        }
        .ivu-modal{
            top: 200px !important;
        }
    }
</style>